import React, { useState, useEffect, useContext } from "react";
import CitizenApplyApiList from "../../Components/CitizenApplyApiList";
import { useNavigate } from "react-router-dom";
import axios from "axios";

function SearchBox() {
  const navigate = useNavigate();

  const { api_getAllUlb } = CitizenApplyApiList();

  // const [searchOpen, setsearchOpen] = useState('hidden')

  // const searchToggle = () => {
  //     searchOpen == 'hidden' ? setsearchOpen('') : setsearchOpen('hidden')
  // }

  const [search, setSearch] = useState("");
  const [filterSuggestion, setFilterSuggestion] = useState([]);

  const suggestions = ["property", "trade", "water", "property1", "trade1"];

  const [suggest, setSuggest] = useState(false);
  const [refresh, setRefresh] = useState(0);

  // const vals = useContext(contextVar)

  const [selectUlb, setselectUlb] = useState([]);

  useEffect(() => {
    axios.get(api_getAllUlb).then(function (response) {
      // handle success
      console.log("ulbid ", response.data);
      setselectUlb(response.data);
    });
  }, []);

  const handleChange = (e) => {
    let ulbids = e.target.value;
    console.log("ulbids : ", ulbids);
    axios
      .get("http://localhost:3333/Select/" + ulbids)
      .then(function (response) {
        // handle success
        console.log("ulbidnews ", response.data.news);
        // vals.ulbdatafun(response.data)
      });
    e.preventDefault();
  };

  const handleSearch = (e) => {
    alert("you searched for", e.target.value);
  };

  const handleSearchList = (e) => {
    if (search != "") {
      setRefresh(refresh + 1);
      setSuggest(true);
    } else {
      setSuggest(false);
    }
  };

  useEffect(() => {
    const result = suggestions.filter((datalist) => {
      return datalist.toLowerCase().match(search.toLocaleLowerCase());
    });
    setFilterSuggestion(result);
  }, [refresh]);

  return (
    <>
      <div>
        <div className=" relative inline-block text-left z-50 ">
          <div class={` flex lg: w-80 h-8`}>
            <select
              class="flex-shrink-0  inline-flex items-center py-1 px-0 text-sm font-medium  text-gray-600 bg-gray-100 border border-gray-300 rounded-l-full hover:bg-gray-100  darks:bg-gray-500 darks:hover:bg-gray-100  darks:text-white darks:border-gray-600 w-28"
              onChange={handleChange}
            >
              <option>select ulb</option>
              {selectUlb.map((items) => (
                <option value={items.id}>{items.ulb_name}</option>
              ))}
            </select>
            <div class="relative w-full">
              <div>
                <input
                  type="search"
                  id="search-dropdown"
                  class=" p-1.5 w-full  text-sm text-gray-900 bg-gray-50 rounded-r-full border-l-gray-50 border-l-1 border border-gray-300 darks:bg-gray-700 darks:border-l-gray-700  darks:border-gray-600 darks:placeholder-gray-600 darks:text-white h-8"
                  name="search"
                  placeholder="Search ..."
                  required
                  onKeyUp={handleSearchList}
                  onChange={(e) => {
                    setSearch(e.target.value);
                    console.log("search => ", search);
                  }}
                  value={search}
                />
              </div>
              <div>
                <ul
                  className={
                    suggest
                      ? `bg-gray-50 rounded-md px-4 text-sm py-2 flex flex-col space-y-2 cursor-pointer`
                      : null
                  }
                >
                  {filterSuggestion.map((elem) => (
                    <>
                      <li
                        className={
                          (suggest ? `visible` : `hidden`) + ` uppercase`
                        }
                        onClick={() => navigate(`/${elem}`)}
                      >
                        {elem}
                      </li>
                    </>
                  ))}
                </ul>
              </div>
              <button
                type="submit"
                class="absolute top-0 right-0 p-1.5 h-8 text-sm font-medium text-black bg-gray-50  rounded-r-full border border-bg-gray-50  hover:bg-gray-50  focus:ring-4 focus:outline-none focus:ring-blue-300 darks:bg-gray-50  darks:hover:bg-gray-50  darks:focus:bg-gray-50 "
              >
                <svg
                  aria-hidden="true"
                  class="w-5 h-5"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  ></path>
                </svg>
                <span class="sr-only">Search</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default SearchBox;

